import { css } from '@emotion/react'
import React from 'react'
import { BiRocket, BiStore, BiLineChart } from 'react-icons/bi'
import { NavLink } from 'react-router-dom'
import palette from '../../lib/palette'

export type GlobalNavProps = {}

function GlobalNav({}: GlobalNavProps) {
  return (
    <div css={block}>
      <NavLink css={item} to="/workspaces" exact>
        <BiRocket />
        <div>Work</div>
      </NavLink>
      <NavLink css={item} to="/" exact>
        <BiStore />
        <div>Sales</div>
      </NavLink>
      <NavLink css={item} to="/" exact>
        <BiLineChart />
        <div>Reports</div>
      </NavLink>
    </div>
  )
}

export default GlobalNav

const block = css`
  display: flex;
  width: 100%;
  justify-content: space-around;
`
const item = css`
  text-decoration: none;
  cursor: pointer;
  color: ${palette.dark['dec']};
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 0.75rem;
  svg {
    font-size: 1.75rem;
  }
`
